import * as React from "react";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface CardProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export default class Card extends NamespaceWrapper<CardProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div className={this.classes("Card", className)} {...props} />;
  }
}

export interface CardMediaProps extends React.ImgHTMLAttributes<HTMLImageElement>, NamespaceWrapperProps {
}

export class CardMedia extends NamespaceWrapper<CardMediaProps> {
  render() {
    const {namespace, className, children, ...props} = this.props;

    return <div className={this.classes("Card__media", className)}>
      <img {...props} />
      {children && <div className={this.classes("Card__media__extra", "Card__media__extra--inverse")}>
        <div className={this.classes("Card__media__legend")}>
          {children}
        </div>
      </div>}
    </div>;
  }
}

