export interface CardHeaderProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Should it display a divider from the main content ?
   */
  divided ?: boolean;
}

export class CardHeader extends NamespaceWrapper<CardHeaderProps> {
  render() {
    const {namespace, className, divided, ...props} = this.props;
    return <div {...props}
                className={this.classes("Card__header", divided && "has-divider", className)} />;
  }
}

